@font-face {
  font-family: Roboto-Regular;
  src: url("../fonts/Roboto/Roboto-Regular.ttf");
}
@font-face {
  font-family: Roboto-Medium;
  src: url("../fonts/Roboto/Roboto-Medium.ttf");
}
@font-face {
  font-family: Roboto-Bold;
  src: url("../fonts/Roboto/Roboto-Bold.ttf");
}
/*--> Common styles START <--*/
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 10pt;
}
a {
  outline: none;
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
  width: 1050px;
}
/*--> Common styles END <--*/
/*--> Header START <--*/
.header {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  min-height: 545px;
  height: 100vh;
}
.header-top-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #f2f2f2;
  height: 46px;
  min-height: 46px;
}
.header-top-bar-logo {
  display: flex;
}
.header-top-bar-logo__img {
  display: flex;
  height: 46px;
}
.header-top-bar-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-grow: 1;
}
.header-top-bar-nav__list {
  display: flex;
  list-style-type: none;
  flex-direction: row;
  justify-content: space-between;
  width: 450px;
  text-transform: uppercase;
}
.header-top-bar-nav__list--item a {
  color: #313131;
}
.header-top-bar-nav__list--item_active a {
  color: #313131;
  font-weight: bold;
}
.header-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-grow: 1;
  min-height: 340px;
  background: url("../img/people-coffee-tea-meeting.jpg");
  background-size: cover;
}
.header-slider-container {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-grow: 1;
}
.header-slider-container-title {
  display: flex;
  width: auto;
  flex-grow: 1;
  text-transform: uppercase;
  font-size: 3.3rem;
  color: #f2f2f2;
  background-color: rgba(0,0,0,0.35);
  padding: 7px 13px;
}
.header-slider-container-description {
  display: flex;
  flex-grow: 1;
  text-transform: uppercase;
  font-size: 1rem;
  margin-bottom: 50px;
  color: rgba(0,0,0,0.66);
  font-weight: 700;
}
.header-slider-container-form {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  justify-content: space-between;
  height: 50px;
  width: 320px;
}
.header-slider-container-form__join-btn {
  display: flex;
  width: 150px;
  background-color: rgba(0,0,0,0.35);
  border: 2px solid #f2f2f2;
  border-radius: 4px;
  color: #f2f2f2;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  font-weight: 600;
}
.header-slider-container-form__works-btn {
  display: flex;
  width: 150px;
  background-color: rgba(0,0,0,0.35);
  border: 2px solid #f2f2f2;
  border-radius: 4px;
  color: #f2f2f2;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  font-weight: 600;
}
.header-slider-container-form__join-btn_active,
.header-slider-container-form__works-btn_active {
  display: flex;
  width: 150px;
  background-color: #73b3bc;
  border: 2px solid #73b3bc;
  border-radius: 4px;
  color: #f2f2f2;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;
  font-weight: 600;
}
.header .header-status {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-grow: 1;
  min-height: 80px;
  background-color: #414141;
}
.header .header-status-line {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  margin: 0 auto;
  height: 50px;
  background-color: #fff;
}
.header .header-status-line__lable {
  display: flex;
  padding: 0 20px;
  background-color: #e85454;
  flex-grow: 1;
  align-items: center;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  color: #f2f2f2;
}
.header .header-status-line__text {
  display: flex;
  padding: 0 10px;
  flex-grow: 1;
  align-items: center;
  text-align: center;
  background-color: #313131;
  color: #ccc8c8;
  text-transform: uppercase;
}
.header .header-partner-logos {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-grow: 1;
  min-height: 80px;
  background-color: #f2f2f2;
}
.header .header-partner-logos__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-grow: 1;
}
.header .header-partner-logos__list--item {
  display: flex;
}
.header .header-partner-logos__list--item img {
  display: flex;
  height: 50px;
}
/*--> Header END <--*/
/*--> About us START <--*/
.about-us {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  min-height: 545px;
  height: 100vh;
  background-color: #11b8cc;
}
.about-us .about-us-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  height: 100px;
  background-color: #424242;
}
.about-us .about-us-title .about-us-title__name {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: #fff;
}
.about-us .about-us-container {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}
.about-us .about-us-container .about-us__who-we-are {
  display: flex;
  flex-grow: 1;
  flex-direction: row;
  background-color: transparent;
}
.about-us .about-us-container .about-us__who-we-are .about-us__who-we-are--inform {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__who-we-are .about-us__who-we-are--images {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__who-we-are .about-us__who-we-are--images .about-us__who-we-are--images_hexagon {
  width: 100px;
  height: 55px;
  background: #f00;
  position: relative;
}
.about-us .about-us-container .about-us__who-we-are .about-us__who-we-are--images .about-us__who-we-are--images_hexagon :before {
  content: "";
  position: absolute;
  top: -25px;
  left: 0;
  width: 0;
  height: 0;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-bottom: 25px solid #f00;
}
.about-us .about-us-container .about-us__who-we-are .about-us__who-we-are--images .about-us__who-we-are--images_hexagon :after {
  content: "";
  position: absolute;
  bottom: -25px;
  left: 0;
  width: 0;
  height: 0;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  border-top: 25px solid #f00;
}
.about-us .about-us-container .about-us__what-we-do {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__what-we-do .about-us__what-we-do--inform {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__what-we-do .about-us__what-we-do--images {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__how-we-do-it {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__how-we-do-it .about-us__how-we-do-it--inform {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
.about-us .about-us-container .about-us__how-we-do-it .about-us__how-we-do-it--images {
  display: flex;
  flex-grow: 1;
  background-color: transparent;
}
/*--> About us END <--*/
/*--> Our skills START <--*/
.our-skills {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100vh;
}
.our-skills-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-height: 80px;
  flex-grow: 1;
  background-color: #424242;
}
.our-skills-title__name {
  display: flex;
  margin: 0 auto;
  font-size: 3rem;
  color: #f2f2f2;
  text-transform: uppercase;
}
.our-skills-container {
  display: flex;
  flex-grow: 1;
  min-height: 582px;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: flex-start;
}
.our-skills-container-work-quality {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
}
.our-skills-container-work-quality__title {
  display: flex;
  text-transform: uppercase;
  font-size: 1.8em;
  color: #11b8cc;
  flex-grow: 1;
}
.our-skills-container-work-quality-container {
  display: flex;
  flex-direction: row;
  align-self: stretch;
  flex-grow: 1;
}
.our-skills-container-work-quality-container__list {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  justify-content: space-around;
  width: 500px;
}
.our-skills-container-work-quality-container__list--item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 130px;
  width: 130px;
  overflow: hidden;
  border-radius: 50%;
  color: #f2f2f2;
  box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.7);
}
.our-skills-container-work-quality-container__list--item__icon {
  display: flex;
  max-width: 130px;
  padding-top: 3px;
  align-items: center;
  justify-content: center;
  flex-grow: 2;
  font-size: 2rem;
}
.our-skills-container-work-quality-container__list--item__description {
  display: flex;
  justify-content: center;
  padding-top: 5px;
  flex-grow: 1;
  font-size: 0.8em;
  width: 130px;
  font-weight: 600;
  background-color: #424242;
  text-transform: capitalize;
}
.our-skills-container-work-quality-container__list--item:nth-child(1) {
  background-color: #f16529;
}
.our-skills-container-work-quality-container__list--item:nth-child(2) {
  background-color: #227ab5;
}
.our-skills-container-work-quality-container__list--item:nth-child(3) {
  background-color: #e85454;
}
.our-skills-container-work-quality-container__list--item:nth-child(4) {
  background-color: #0eae8c;
}
.our-skills-container-work-quality-container__list--item:nth-child(5) {
  background-color: #73b3bc;
}
/*--> Our skills END <--*/
